<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="editor">
        <el-card>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="160px" class="u-edit-from" label-position="left">
                <el-form-item label="原子课名称：" prop="atomName">
                    <el-input v-model="ruleForm.atomName" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="包装原子课名称：">
                    <el-input v-model="ruleForm.packAtomName" placeholder="请输入包装原子课名称"></el-input>
                </el-form-item>
                <el-form-item label="教研标记：" prop="teachMark">
                    <div class="tag-list">
                        <el-tag v-for="(n, index) in ruleForm.teachMark" :key="index">健康护理指导</el-tag>
                    </div>
                </el-form-item>
                <el-form-item label="课程分类：" prop="courseSort">
                    <div class="tag-list">
                        <el-tag v-for="(n, index) in ruleForm.courseSort" :key="index">患教课程</el-tag>
                    </div>
                </el-form-item>
                <el-form-item label="所属知识点：" prop="belongKnowledge">
                    <div class="tag-list">
                        <el-tag v-for="(n, index) in ruleForm.belongKnowledge" :key="index">健康护理学习</el-tag>
                    </div>
                </el-form-item>
                <el-form-item label="授课老师：">
                    <el-input v-model="ruleForm.teacher" placeholder="请输入授课老师"></el-input>
                </el-form-item>
                <el-form-item label="包装原子课介绍：" prop="packAtomCourse">
                    <RichText  v-model="ruleForm.packAtomCourse" />
                </el-form-item>
                <el-form-item label="运营标签：" prop="operateLabel">
                    <SelectTags v-model="ruleForm.operateLabel"></SelectTags>
                </el-form-item>
                <el-form-item label="售卖类型：" prop="salesType">
                    <el-select
                            v-model="ruleForm.salesType"
                            clearable
                            placeholder="请选择售卖类型">
                        <el-option
                            v-for="v in $Cfg.sellTypes"
                            :key="v.id"
                            :label="v.name"
                            :value="v.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="售卖金额：" prop="salesMoney">
                    <el-input v-model="ruleForm.salesMoney" placeholder="请输入金额"></el-input>
                </el-form-item>
                <el-form-item label="折扣类型：" prop="discountType">
                    <el-select
                            v-model="ruleForm.discountType"
                            clearable
                            placeholder="请选择折扣类型"
                    >
                        <el-option v-for="(v,k) in $Cfg.discountTypes" :key="k" :label="v" :value="k"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="限时免费：">
                    <el-radio v-model="ruleForm.limtFree" label="1">是</el-radio>
                    <el-radio v-model="ruleForm.limtFree" label="2">否</el-radio>
                </el-form-item>
                <el-form-item label="限时时间：" prop="limitDate">
                    <el-date-picker
                        v-model="ruleForm.limitDate"
                        type="datetimerange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="有效天数：" prop="effectiveDays">
                    <el-input v-model="ruleForm.effectiveDays" placeholder="请输入天数"></el-input>
                </el-form-item>
                <el-form-item label="上线时间：" prop="uptime">
                    <el-date-picker type="date" placeholder="请选择上线时间" v-model="ruleForm.uptime"></el-date-picker>
                </el-form-item>
                <el-form-item label="课程封面：" class="u-upload-image">
                    <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :show-file-list="false"
                            :on-success="handleCover">
                        <img v-if="imageUrl" :src="imageUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <div>
                    <el-button type="primary" @click="submitPack">包装完成</el-button>
                </div>
            </el-form>
            <div class="multiple-title-card">
                <div class="title">课后练习题部分</div>
                <Knowledge />
            </div>
            <div class="multiple-title-card">
                <div class="title">知识点部分</div>
                <Practice />
            </div>
        </el-card>
    </div>
</template>

<script>
    import RichText from '@/components/RichText/RichText.vue'
    import SelectTags from '@/components/SelectTags/SelectTags.vue'
    import Knowledge from '../components/knowledge/Index.vue'
    import Practice from '../components/practice/Index.vue'

    export default {
        data() {
            return {
                ruleForm: {
                    atomName: '',
                    packAtomName: '',
                    teachMark: ['1', '2'],
                    courseSort: ['1', '2'],
                    belongKnowledge: ['1', '2'],
                    teacher: '',
                    packAtomCourse: '',
                    operateLabel: [],
                    salesType: '',
                    salesMoney: '',
                    discountType: '',
                    limitDate: '',
                    limtFree: '',
                    effectiveDays: '',
                    uptime: '',
                    imageUrl: ''
                },
                rules: {
                    atomName: [
                        { required: true, message: '原子课名称不能为空', trigger: 'blur' }
                    ],
                    teachMark: [
                        { type: 'array', required: true, message: '教研标签不能为空' }
                    ],
                    courseSort: [
                        { required: true, message: '课程分类不能为空' }
                    ],
                    belongKnowledge: [
                        { required: true, message: '所属知识点不能为空' }
                    ],
                    packAtomCourse: [
                        { required: true, message: '包装原子课介绍不能为空' }
                    ],
                    operateLabel: [
                        { required: true, message: '运营标签不能为空', trigger: 'click' }
                    ],
                    salesType: [
                        { required: true,  message: '售卖类型不能为空', trigger: 'change' }
                    ],
                    salesMoney: [
                        { required: true, message: '售卖金额不能为空', trigger: 'blur' }
                    ],
                    limitDate: [
                        { required: true, message: '限时时间不能为空', trigger: 'change'}
                    ],
                    effectiveDays: [
                        { required: true, message: '有效天数不能为空', trigger: 'blur' }
                    ],
                    uptime: [
                        { type: 'date', required: true, message: '上线时间不能为空', trigger: 'change' }
                    ]
                }
            }
        },
        methods: {
            handleCover (res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            submitPack() {
                this.$refs.ruleForm.validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        },
        created() {

        },
        components: {
            RichText, SelectTags, Knowledge, Practice
        }
    }
</script>
